import React from "react";

const Modal = () => {
  let [modal, setModal] = React.useState(false);//alterado para dev
  let [modalContent, setModalContent] = React.useState(null);//alterado para dev

  let handleModal = (content = false) => {
    setModal(!modal);
    if (content) {
      setModalContent(content);
    }
  };

  return { modal, handleModal, modalContent };
};

export default Modal;