import styled from 'styled-components'

export const TitleBar = styled.div`
position: fixed;
display: flex;
flex: 1;
align-items: center;
justify-content: space-between;
width: 87vw;
height: 5vh;
right: 0px;
top: 0px;
background: #1C3E7E;
padding: 0 25px 0 5px;
`
export const Row = styled.div`
display: flex;
flex-direction: row;
`
export const Col = styled.div`
display: flex;
flex-direction: column;
`
export const Title = styled.h1`
margin-left: 10px;
font-weight: 500;
font-size: 16pt;
color: white;
`
export const Button = styled.button`
width: 140px;
color: ${props => props.color ?? "white"};
margin: 0 0 0 5px;
border:none;
cursor: pointer;
height: 38px;
background: ${props => props.bgColor};
border-radius: 4px;
font-weight: 500;
font-size: 15px;
:hover{
  background: #2D323E;
  color: white;
}
`
export const UserButton = styled.button`
width: 50px;
color: ${props => props.color ?? "white"};
margin: 2px 2px;
border:none;
cursor: pointer;
height: 28px;
background: ${props => props.bgColor};
border-radius: 4px;
font-weight: 500;
font-size: 10px;
:hover{
  background: #2D323E;
  color: white;
}
`
export const Table = styled.div`
width: 100%;
color: #919098;
border:none;
border-spacing: 0;
`
export const TableRow = styled.tr`
cursor: pointer;
font-style: normal;
font-weight: normal;
font-size: 12px;
line-height: 8px;
&:hover{
  background-color: rgba(124, 155, 2011, 0.08);
}
&:active{
  background-color: rgba(124, 155, 2011, 0.2);
}
td {
  padding: 0 10px;
}
`
export const TableHead = styled.thead`
color: black;
font-style: normal;
font-weight: bold;
font-size: 14px;
line-height: 12px;
text-align: left;
th {
  padding: 10px;
}
`
export const Pagination = styled.ul`
  display: flex;
  list-style: none;
  img{
  width: 20px;
  height: 20px;
  }
  li{
  cursor: pointer;
  padding: 2px;
  margin-left: -1px;
  width: 28px;
  height: 25px;
  border: 1px solid rgb(216, 216, 216);
	text-align: center;
  font-size: 1em;
  background-color: white;
  &:last-child{
  border-radius: 0px 5px 5px 0px ;
  }
  &:first-child{
  border-radius: 5px 0px 0px 5px ;
  }
  &:hover{
  background-color: rgba(215, 93, 233, .2);
}
  &:active{
  background-color: rgba(215, 93, 233, .5);
  color: #fafafa;
}
}
`
export const Input = styled.input`
display: flex;
margin: 5px;
height: 38px;
width: 20vw;
padding: 5px;
font-size: 11pt;

background: #FFFFFF;
border: 1px solid #DDDDDD;
border-radius: 4px;
`
export const Select = styled.select`
display: flex;
width: 100%;
padding: 5px;
font-size: 12pt;
height: 40px;
background: #FFFFFF;
border: 1px solid #DDDDDD;
border-radius: 4px;
`
export const TextHeader = styled.h1`
color: black;
font-style: normal;
font-weight: bold;
font-size: 14px;
line-height: 18px;
text-align: left;
`
export const Text = styled.h1`
font-style: normal;
font-weight: normal;
font-size: 12px;
line-height: 18px;
`