import styled from 'styled-components'

export const Container = styled.div`
width: 745px;
padding: 5px;
margin: 5px;
align-items: center;
justify-content: center;
flex-direction: column;
`
export const Title = styled.h5`
color: white;
font-weight: bold;
font-size: 16px;
line-height: 3;
text-align: center;
flex: .9;
`
export const TextBold = styled.p`
margin: 2px 0;
font-weight: bold;
font-size: 14px;
`
export const TextSimple = styled.span`
margin: 0px 0;
font-weight: normal;
font-size: 12px;
`
export const Row = styled.div`
display: flex;
flex-direction: row;
`
export const Col = styled.div`
display: flex;
flex: 1;
flex-direction: column;
justify-content: space-between;
`
export const Button = styled.button`
width: 140px;
color: ${props => props.color ?? "white"};
margin: 5px;
border:none;
cursor: pointer;
height: 38px;
background: ${props => props.bgColor};
border-radius: 4px;
font-weight: 500;
font-size: 15px;
:hover{
  background: #2D323E;
  color: white;
}
`
export const UserButton = styled.button`
width: 50px;
color: ${props => props.color ?? "white"};
margin: 2px 2px;
border:none;
cursor: pointer;
height: 32px;
background: ${props => props.bgColor};
border-radius: 4px;
font-weight: 500;
font-size: 12px;
:hover{
  background: #2D323E;
  color: white;
}
`
export const Table = styled.table`
margin-top: 10px;
width: 740px;
border:none;
border-spacing: 0;
`
export const TableRow = styled.tr`
cursor: pointer;
font-size: 12px;
line-height: 24px;
font-weight: 400;
text-align: left;
td {
  border-top: 1px solid #DCDCDC;
  &:last-child{
  text-align: right;
}
}
`
export const TableHead = styled.thead`
width: 755px;
padding: 5px 0;
color: black;
font-size: .4em;
text-align: left;
th {
  padding: 5px 0;
  &:last-child{
  text-align: right;
  }
}
`
export const Pagination = styled.ul`
  display: flex;
  list-style: none;
  img{
  width: 20px;
  height: 20px;
  }
  li{
  cursor: pointer;
  padding: 2px;
  margin-left: -1px;
  width: 28px;
  height: 25px;
  border: 1px solid rgb(216, 216, 216);
	text-align: center;
  font-size: 1em;
  background-color: white;
  &:last-child{
  border-radius: 0px 5px 5px 0px ;
  }
  &:first-child{
  border-radius: 5px 0px 0px 5px ;
  }
  &:hover{
  background-color: rgba(215, 93, 233, .2);
}
  &:active{
  background-color: rgba(215, 93, 233, .5);
  color: #fafafa;
}
}
`
export const FormGroup = styled.div`
display: flex;
flex-direction: row;
`
export const TopBar = styled.div`
display: flex;
width: 755px;
height: 45px;
background: #1C3E7E;
`
export const Img = styled.img`
height: 35px;
margin: 5px 10px;
`
